"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { 
  LogOut, Activity, Sparkles, UserCheck, Eye, ShieldCheck, Zap, 
  ChevronLeft, ChevronRight, Loader2, Slack, Send 
} from "lucide-react"

// 인터페이스 정의
interface AdvisorRequest {
  userId: string;     
  userName: string;   
  name: string;
  email: string;
  appliedAt: string;
  reason: string;     
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  // 상태 관리
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)

  // 페이징
  const [advisorPage, setAdvisorPage] = useState(1)
  const itemsPerPage = 5

  // 베이스 URL 설정
  const BASE_URL = "http://34.50.7.8/v1/user"

  // --- 1. 전문가 신청 대기 목록 조회 API ---
  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    if (!token) return

    try {
      setIsLoading(true)
      const res = await fetch(`${BASE_URL}/admin/advisor/pending`, {
        method: "GET",
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        }
      })
      const data = await res.json()
      
      if (data.isSuccess) {
        setAdvisorRequests(data.result) // AdvisorCandidateResponse 리스트 매핑
      } else {
        console.error("조회 실패:", data.message)
      }
    } catch (error) {
      console.error("API 연결 오류:", error)
    } finally {
      setIsLoading(false)
    }
  }, [BASE_URL])

  // 권한 체크 및 초기화
  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "Admin"
    
    if (role !== "MANAGER" && role !== "MASTER") {
      alert("접근 권한이 없습니다.")
      router.push("/login")
      return
    }

    setUserRole(role)
    setUserName(name)
    setIsMounted(true)
    fetchPendingAdvisors()
  }, [router, fetchPendingAdvisors])

  // --- 2. 특정 유저 전문가 승인 API ---
  const handleApproveAdvisor = async (userId: string) => {
    const token = localStorage.getItem("accessToken")
    if (!confirm("해당 유저를 어드바이저로 승인하시겠습니까?")) return

    try {
      const res = await fetch(`${BASE_URL}/admin/advisor/${userId}/approve`, {
        method: "POST",
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        }
      })
      const data = await res.json()
      
      if (data.isSuccess) {
        alert(data.message || "승인이 완료되었습니다.")
        setIsDetailOpen(false)
        fetchPendingAdvisors() // 목록 리프레시
      } else {
        alert("승인 실패: " + data.message)
      }
    } catch (error) {
      alert("서버 통신 오류가 발생했습니다.")
    }
  }

  const handleLogout = () => {
    if (confirm("로그아웃 하시겠습니까?")) {
      localStorage.clear()
      router.push("/login")
    }
  }

  const currentAdvisors = advisorRequests.slice((advisorPage - 1) * itemsPerPage, advisorPage * itemsPerPage)
  const totalAdvisorPages = Math.ceil(advisorRequests.length / itemsPerPage)

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      {/* HEADER */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-black text-[10px] italic">
              {userRole} ADMIN
            </Badge>
          </Link>
          <div className="flex items-center gap-4">
            <div className="hidden md:block text-right">
              <p className="text-sm font-bold leading-none">{userName}님</p>
              <p className="text-[10px] text-muted-foreground uppercase tracking-widest mt-1 font-black italic">System Controller</p>
            </div>
            <Avatar className="h-9 w-9 border-2 border-primary/20 shadow-sm">
              <AvatarFallback className="bg-primary text-primary-foreground text-xs font-black uppercase">{userName?.charAt(0)}</AvatarFallback>
            </Avatar>
            <Button variant="ghost" size="icon" onClick={handleLogout} className="rounded-xl hover:bg-destructive/10 hover:text-destructive">
              <LogOut className="h-5 w-5" />
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="flex justify-between items-end mb-8">
          <h1 className="text-4xl font-black tracking-tighter uppercase italic">Control <span className="text-primary">Panel</span></h1>
          <Button variant="outline" size="sm" className="rounded-xl font-bold bg-white" onClick={fetchPendingAdvisors}>
            <Activity className="h-4 w-4 mr-2 text-primary" /> 실시간 리프레시
          </Button>
        </div>

        {/* 대시보드 요약 */}
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card className="p-6 border-border shadow-none rounded-2xl bg-secondary/5">
            <div className="h-10 w-10 rounded-xl bg-primary/10 flex items-center justify-center mb-4"><Zap className="h-5 w-5 text-primary" /></div>
            <p className="text-[10px] text-muted-foreground mb-1 font-black uppercase italic tracking-tighter">Status</p>
            <p className="text-2xl font-black uppercase tracking-tight">Healthy</p>
          </Card>
          <Card className="p-6 border-border shadow-none rounded-2xl bg-orange-500/5">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-xl bg-orange-500/10 flex items-center justify-center"><UserCheck className="h-5 w-5 text-orange-500" /></div>
              <Badge variant="destructive" className="animate-pulse rounded-full font-black">{advisorRequests.length}</Badge>
            </div>
            <p className="text-[10px] text-muted-foreground mb-1 font-black uppercase italic tracking-tighter">Pending Task</p>
            <p className="text-2xl font-black uppercase tracking-tight italic text-orange-600">Review Required</p>
          </Card>
          <Card className="p-6 border-border shadow-none rounded-2xl">
             <div className="h-10 w-10 rounded-xl bg-accent/10 flex items-center justify-center mb-4"><ShieldCheck className="h-5 w-5 text-accent" /></div>
             <p className="text-[10px] text-muted-foreground mb-1 font-black uppercase italic tracking-tighter">Permission</p>
             <p className="text-2xl font-black uppercase tracking-tight">{userRole} Mode</p>
          </Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2">
            <Card className="p-8 border-border shadow-none flex flex-col min-h-[550px] rounded-3xl relative overflow-hidden bg-card">
              <div className="flex items-center justify-between mb-8">
                <h2 className="text-2xl font-black italic tracking-tighter flex items-center gap-2 uppercase">
                  <UserCheck className="h-7 w-7 text-primary" /> Advisor Candidates
                </h2>
              </div>

              {isLoading ? (
                <div className="flex-1 flex items-center justify-center"><Loader2 className="h-10 w-10 animate-spin text-primary" /></div>
              ) : (
                <div className="flex-1 space-y-4">
                  {currentAdvisors.length > 0 ? currentAdvisors.map((req) => (
                    <div key={req.userId} className="flex items-center justify-between p-5 rounded-[20px] bg-secondary/10 border border-transparent hover:border-primary/20 hover:bg-white transition-all shadow-sm group">
                      <div className="flex items-center gap-4">
                        <Avatar className="h-12 w-12 border-2 border-white shadow-md">
                          <AvatarFallback className="font-black bg-primary text-white italic">{req.name[0]}</AvatarFallback>
                        </Avatar>
                        <div>
                          <p className="font-black text-base tracking-tight leading-none mb-1">{req.name}</p>
                          <p className="text-[10px] text-muted-foreground font-mono uppercase tracking-tighter">{req.userName} · {req.appliedAt.split('T')[0]}</p>
                        </div>
                      </div>
                      <Button variant="outline" size="sm" className="h-10 font-black uppercase italic text-[11px] rounded-xl px-5 border-border group-hover:bg-primary group-hover:text-white transition-colors" onClick={() => { setSelectedRequest(req); setIsDetailOpen(true); }}>
                        <Eye className="h-4 w-4 mr-2" /> Detail Review
                      </Button>
                    </div>
                  )) : (
                    <div className="flex-1 flex flex-col items-center justify-center text-muted-foreground italic py-20">
                       <p className="text-xs font-black uppercase tracking-[0.3em] opacity-30">No Applications Found</p>
                    </div>
                  )}
                </div>
              )}
              
              <div className="flex items-center justify-center gap-2 mt-8 pt-6 border-t border-border/50">
                <Button variant="outline" size="icon" className="h-9 w-9 rounded-xl" onClick={() => setAdvisorPage(p => Math.max(1, p-1))} disabled={advisorPage === 1}><ChevronLeft className="h-4 w-4" /></Button>
                <span className="text-[11px] font-black px-6 italic uppercase tracking-widest">{advisorPage} / {totalAdvisorPages || 1}</span>
                <Button variant="outline" size="icon" className="h-9 w-9 rounded-xl" onClick={() => setAdvisorPage(p => Math.min(totalAdvisorPages, p+1))} disabled={advisorPage === totalAdvisorPages || totalAdvisorPages === 0}><ChevronRight className="h-4 w-4" /></Button>
              </div>
            </Card>
          </div>

          <div className="space-y-6">
            <Card className="p-8 border-border shadow-none bg-secondary/5 rounded-3xl min-h-[300px]">
              <h2 className="text-xl font-black italic tracking-tighter mb-6 flex items-center gap-2 uppercase">
                <ShieldCheck className="h-6 w-6 text-primary" /> Guide
              </h2>
              <div className="space-y-6 text-[13px] font-medium text-muted-foreground leading-relaxed">
                <p className="italic">전문가 신청 내역을 검토한 후 승인 버튼을 누르면 해당 유저는 즉시 <span className="text-primary font-bold">ADVISOR</span> 권한을 획득하게 됩니다.</p>
                <div className="p-4 bg-white rounded-2xl border border-border/50">
                  <p className="text-[10px] font-black uppercase text-primary mb-2 italic">Caution</p>
                  <p className="text-xs">승인 취소 기능은 준비 중이오니 신중하게 심사해 주시기 바랍니다.</p>
                </div>
              </div>
            </Card>
            
            <Button className="w-full h-16 rounded-3xl bg-primary text-primary-foreground font-black uppercase italic tracking-tighter text-lg shadow-xl shadow-primary/20 group">
              <Slack className="h-6 w-6 mr-3 group-hover:rotate-12 transition-transform" /> Send Slack Report
            </Button>
          </div>
        </div>
      </main>

      {/* 심사 상세 모달 */}
      <Dialog open={isDetailOpen} onOpenChange={setIsDetailOpen}>
        <DialogContent className="sm:max-w-[500px] rounded-[2.5rem] p-0 border-none overflow-hidden shadow-2xl">
          <div className="h-32 bg-primary relative flex items-end p-8">
            <div className="absolute top-0 right-0 p-6 opacity-10"><UserCheck className="h-32 w-32 text-white" /></div>
            <DialogTitle className="text-white font-black text-3xl uppercase italic tracking-tighter">Candidate<br/>Verification</DialogTitle>
          </div>
          
          {selectedRequest && (
            <div className="p-8 space-y-6 bg-white">
              <div className="flex items-center gap-5 p-6 rounded-[2rem] bg-secondary/10 border border-border shadow-inner">
                <Avatar className="h-20 w-20 border-4 border-white shadow-xl">
                  <AvatarFallback className="font-black text-2xl bg-primary text-white italic">{selectedRequest.name[0]}</AvatarFallback>
                </Avatar>
                <div>
                  <p className="font-black text-2xl leading-none tracking-tighter mb-2">{selectedRequest.name}</p>
                  <p className="text-xs text-muted-foreground font-mono uppercase tracking-tighter mb-1">{selectedRequest.email}</p>
                  <Badge variant="outline" className="bg-white text-[9px] font-black uppercase border-primary/20 text-primary">ID: {selectedRequest.userId.slice(0,8)}</Badge>
                </div>
              </div>

              <div className="space-y-3">
                <p className="text-[10px] font-black uppercase tracking-[0.2em] text-muted-foreground italic pl-2">Professional Summary</p>
                <div className="p-7 bg-secondary/5 border-2 border-dashed border-secondary rounded-[2rem] text-[15px] leading-relaxed italic font-medium text-slate-700">
                  "{selectedRequest.reason}"
                </div>
              </div>

              <DialogFooter className="gap-3 pt-4">
                <Button variant="ghost" className="flex-1 font-black h-14 rounded-2xl uppercase italic tracking-tighter text-muted-foreground" onClick={() => setIsDetailOpen(false)}>Close</Button>
                <Button className="flex-1 font-black h-14 rounded-2xl shadow-xl shadow-primary/30 uppercase italic tracking-tighter text-base" onClick={() => handleApproveAdvisor(selectedRequest.userId)}>Approve Advisor</Button>
              </DialogFooter>
            </div>
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}